import * as _ from 'lodash';
import { Pipe, PipeTransform } from '@angular/core';
@Pipe({
    name: 'destinoDataFilter'
})
export class DestinoFilterPipe implements PipeTransform {
    transform(array: any[], query: string): any {
        if (query) {
            return _.filter(array, row => row.destino.indexOf(query) > -1);
        }
        return array;
    }
}



