import { AuthService } from './../../../../../shared/auth.service';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { AuthLocalstorage } from './../../../../../shared/auth-localstorage.service';
import { RefaccionsService } from './../refaccions.service';
import { RefaccionsInterface } from './../refaccions.interface';
import { Component, OnInit } from '@angular/core';
import { FormGroup, AbstractControl, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { TallersService } from './../../../../tallers/components/tallers-table/tallers.service';
import { TallersAddModalComponent } from './../../../../tallers/components/tallers-table/tallers-add-modal/tallers-add-modal.component';
@Component({
  selector: 'edit-service-modal',
  styleUrls: [('./refaccions-edit-modal.component.scss')],
  templateUrl: './refaccions-edit-modal.component.html'
})
export class RefaccionsEditModalComponent extends DialogComponent<RefaccionsInterface, any> implements OnInit, RefaccionsInterface {
  _taller: string[] = [];

  idrefaccion: number;
  identificador: string;
  nombre: string;
  precioCompra: number;
  precioVenta: number;
  taller_idtaller: number;
  estante: number;
  cantidad: number;
  descripcion: string;
  proveedor: string;
  fecha: string;
  tipoPago: string;
  estadoPago: string;
  fechaPago: string;
  nota: number;

    tallerRewriteable: boolean = false;

  modalHeader: string;
  data: any;
  form: FormGroup;
  submitted: boolean = false;

  nombreAC: AbstractControl;
  identificadorAC: AbstractControl;
  precioCompraAC: AbstractControl;
  precioVentaAC: AbstractControl;
  taller_idtallerAC: AbstractControl;
  estanteAC: AbstractControl;
  cantidadAC: AbstractControl;
  descripcionAC: AbstractControl;
  proveedorAC: AbstractControl;
  fechaAC: AbstractControl;
  tipoPagoAC: AbstractControl;
  estadoPagoAC: AbstractControl;
  fechaPagoAC: AbstractControl;
  notaAC: AbstractControl;

  constructor(
      private service: RefaccionsService,
      private authService: AuthService, 
      private tallersService: TallersService,
      fb: FormBuilder,
      private toastrService: ToastrService,
      private authLocalstorage: AuthLocalstorage,
      dialogService: DialogService,
  ) {
  super(dialogService);
  this.form = fb.group({
    'nombreAC' : ['',Validators.compose([Validators.maxLength(45)])],
    'identificadorAC' : ['',Validators.compose([Validators.maxLength(45)])],
    'precioCompraAC' : ['',Validators.compose([Validators.maxLength(11)])],
    'precioVentaAC' : ['',Validators.compose([Validators.maxLength(11)])],
    'taller_idtallerAC' : ['',Validators.compose([Validators.required,Validators.maxLength(11)])],
    'estanteAC' : ['',Validators.compose([Validators.required,Validators.maxLength(11)])],
    'cantidadAC' : ['',Validators.compose([Validators.required,Validators.maxLength(11)])],
    'descripcionAC' : ['',Validators.compose([Validators.maxLength(150)])],
    'proveedorAC' : ['',Validators.compose([Validators.required,Validators.maxLength(45)])],
    'fechaAC' : [''],
    'tipoPagoAC' : [''],
    'estadoPagoAC' : [''],
    'fechaPagoAC' : [''],
    'notaAC' : ['', Validators.maxLength(9)],
  });
  this.nombreAC = this.form.controls['nombreAC'];
  this.identificadorAC = this.form.controls['identificadorAC'];
  this.precioCompraAC = this.form.controls['precioCompraAC'];
  this.precioVentaAC = this.form.controls['precioVentaAC'];
  this.taller_idtallerAC = this.form.controls['taller_idtallerAC'];
  this.estanteAC = this.form.controls['estanteAC'];
  this.cantidadAC = this.form.controls['cantidadAC'];
  this.descripcionAC = this.form.controls['descripcionAC'];
  this.proveedorAC = this.form.controls['proveedorAC'];
  this.fechaAC = this.form.controls['fechaAC'];
    
    this.tipoPagoAC = this.form.controls['tipoPagoAC'];
    this.estadoPagoAC = this.form.controls['estadoPagoAC'];
    this.fechaPagoAC = this.form.controls['fechaPagoAC'];
    this.notaAC = this.form.controls['notaAC'];

    // Buscar permisos del usuario en el mdulo
    const user = this.authService.useJwtHelper();

    if (user.super) {
        this.tallerRewriteable = true;
    } else {
        const userModules = this.authService.getUserModules();
        if (userModules[0]) {
            for (const element in userModules) {
                if (userModules.hasOwnProperty(element)) {
                    if (userModules[element].path === '/pages/tallers') {
                        this.tallerRewriteable = userModules[element].writeable;
                    }
                } 
            }
        }
    }
  }
  ngOnInit() {
      this.getTaller();
  }

  tallerAddModalShow() {
      const disposable = this.dialogService.addDialog(TallersAddModalComponent)
      .subscribe( data => {
          if (data) {
              this.tallerShowToast(data);
          }
      }); 
  }

  tallerShowToast(result) {
      if (result.success) {
          this.toastrService.success(result.message);
          this.getTaller();
      } else {
          this.toastrService.error(result.message);
      }
  }
  getTaller() {
      this.tallersService.all()
      .subscribe(
          (data: any) => this._taller = data.result,
      );
  }
  confirm() {
      this.result = this.data;
      this.close();
  }
  onSubmit(values: RefaccionsInterface): void {
      this.submitted = true;
      if (this.form.valid) {
          this.service
              .update({
                  idrefaccion: this.idrefaccion,
                  identificador: this.identificador,
                  nombre: this.nombre,
                  precioCompra: this.precioCompra,
                  precioVenta: this.precioVenta,
                  taller_idtaller: this.taller_idtaller,
                  estante: this.estante,
                  cantidad: this.cantidad,
                  descripcion: this.descripcion,
                  proveedor: this.proveedor,
                  fecha: this.fecha,
                  tipoPago: this.tipoPago,
                  estadoPago: this.estadoPago,
                  fechaPago: this.fechaPago,
                  nota: this.nota
              })
              .subscribe(
                  (data: any) => {
                      this.data = data;
                      this.confirm();
              });
          }
  }
}



