import { CortesService } from './../../../pages/cortes/components/cortes-table/cortes.service';
import { CortesInterface } from './../../../pages/cortes/components/cortes-table/cortes.interface';
import { CortesEditModalComponent } from './../../../pages/cortes/components/cortes-table/cortes-edit-modal/cortes-edit-modal.component';
import { CortesAddModalComponent } from './../../../pages/cortes/components/cortes-table/cortes-add-modal/cortes-add-modal.component';
import { DialogService } from 'ng2-bootstrap-modal';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';
import { AuthService } from './../../../shared/auth.service';
import { Component } from '@angular/core';
import { GlobalState } from '../../../global.state';

@Component({
  selector: 'ba-page-top',
  templateUrl: './baPageTop.html',
  styleUrls: ['./baPageTop.scss']
})
export class BaPageTop {

  isScrolled: boolean = false;
  isMenuCollapsed: boolean = false;
  isAuth: boolean;
  openCajaAdministracion: boolean;
  closeCajaAdministracion: boolean;
  openCajaTaller: boolean;
  closeCajaTaller: boolean;
  isCajaTaller: boolean = false;
  isCajaAdministracion: boolean = false;
  user;
  corte: CortesInterface;

  constructor(
    private _state: GlobalState, 
    private dialogService: DialogService, 
    private authService: AuthService,
    private cortesService: CortesService,
    private toastrService: ToastrService,
    private router: Router) {
    this._state.subscribe('menu.isCollapsed', (isCollapsed) => {
      this.isMenuCollapsed = isCollapsed;
    });

    // VALIDAR REA DE EMPLEADO/USUARIO
    this.isAuth = this.authService.isLoggedIn;
    if (this.isAuth) {
      this.user = this.authService.useJwtHelper();
      if (this.user.area === 'CAJA TALLER') {
        this.isCajaTaller = true;
        this.getCorteTaller();
      }
      if (this.user.area === 'CAJA ADMINISTRACION') {
        this.isCajaAdministracion = true;
        this.getCorteAdministracion();
      }
    }
  }

  getCorteTaller() {
    // es rol caja
    this.cortesService.findTallerInThisDay()
      .subscribe((data: any) => {
        if (data.success) {
          if (data.result[0]) {
            this.corte = data.result[0];
            this.closeCajaTaller = true;
            this.openCajaTaller = false;
          } else {
            this.openCajaTaller = true;
            this.closeCajaTaller = false;
          }
        }

      });
  }

  getCorteAdministracion() {
    // es rol caja
    this.cortesService.findAdministracionInThisDay()
      .subscribe((data: any) => {
        if (data.success) {
          if (data.result[0]) {
            this.corte = data.result[0];
            this.closeCajaAdministracion = true;
            this.openCajaAdministracion = false;
          } else {
            this.openCajaAdministracion = true;
            this.closeCajaAdministracion = false;
          }
        }

      });
  }

  buscarOrden(idorden: number, event) {
    event.preventDefault();
      this.router.navigate([`/pages/ordens/${idorden}`]);
  }

  toggleMenu() {
    this.isMenuCollapsed = !this.isMenuCollapsed;
    this._state.notifyDataChanged('menu.isCollapsed', this.isMenuCollapsed);
    return false;
  }

  scrolledChanged(isScrolled) {
    this.isScrolled = isScrolled;
  }

  logout() {
    this.authService.logout();
  }

  showToast(result) {
    if (result.success) {
      this.toastrService.success(result.message);
    } else {
      this.toastrService.error(result.message);
    }
  }

  CorteTallerAddModalShow() {
    const corte = {
      'idcorte': 0,
      'montoInicial': 0,
      'montoFinal': 0,
      'ganancia': 0,
      'inicia_idempleado': 0,
      'finaliza_idempleado': 0,
      'fechaInicia': '',
      'horaInicia': '',
      'fechaFInaliza': '',
      'horaFinaliza': '',
      'baja': false,
      'modified_at': '',
      'created_by': 0,
      'created_at': '',
      'seccion': 'TALLER' };

    const disposable = this.dialogService.addDialog(CortesAddModalComponent, corte)
    .subscribe( data => {
        if (data) {
            this.showToast(data);
            this.getCorteTaller();
        }
    });
  }

  CorteTallerEditModalShow() {
    const disposable = this.dialogService.addDialog(CortesEditModalComponent, this.corte)
    .subscribe( data => {
        if (data) {
            this.showToast(data);
            this.getCorteTaller();
        }
    },
    error => console.log(error),
    () => console.log('Modified complete'));
  }

  CorteAdministracionAddModalShow() {
    const corte = {
      'idcorte': 0,
      'montoInicial': 0,
      'montoFinal': 0,
      'ganancia': 0,
      'inicia_idempleado': 0,
      'finaliza_idempleado': 0,
      'fechaInicia': '',
      'horaInicia': '',
      'fechaFInaliza': '',
      'horaFinaliza': '',
      'baja': false,
      'modified_at': '',
      'created_by': 0,
      'created_at': '',
      'seccion': 'ADMINISTRACION' };

    const disposable = this.dialogService.addDialog(CortesAddModalComponent, corte)
    .subscribe( data => {
        if (data) {
            this.showToast(data);
            this.getCorteAdministracion();
        }
    });
  }
  
  CorteAdministracionEditModalShow() {
    const disposable = this.dialogService.addDialog(CortesEditModalComponent, this.corte)
    .subscribe( data => {
        if (data) {
            this.showToast(data);
            this.getCorteAdministracion();
        }
    },
    error => console.log(error),
    () => console.log('Modified complete'));
  }

}



