import * as _ from 'lodash';
import { Pipe, PipeTransform } from '@angular/core';
@Pipe({
    name: 'persona_choferDataFilter'
})
export class Persona_choferFilterPipe implements PipeTransform {
    transform(array: any[], query: string): any {
        if (query) {
            return _.filter(array, row => row.persona_chofer.indexOf(query) > -1);
        }
        return array;
    }
}



