import { AuthService } from './../../../../../shared/auth.service';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { AuthLocalstorage } from './../../../../../shared/auth-localstorage.service';
import { PermisotaxiestadosService } from './../permisotaxiestados.service';
import { PermisotaxiestadosInterface } from './../permisotaxiestados.interface';
import { Component, OnInit } from '@angular/core';
import { FormGroup, AbstractControl, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { PermisotaxisService } from './../../../../permisotaxis/components/permisotaxis-table/permisotaxis.service';
import { PermisotaxisAddModalComponent } from './../../../../permisotaxis/components/permisotaxis-table/permisotaxis-add-modal/permisotaxis-add-modal.component';
import { EstadoactividadsService } from './../../../../estadoactividads/components/estadoactividads-table/estadoactividads.service';
import { EstadoactividadsAddModalComponent } from './../../../../estadoactividads/components/estadoactividads-table/estadoactividads-add-modal/estadoactividads-add-modal.component';

@Component({
  selector: 'add-service-modal',
  styleUrls: [('./permisotaxiestados-add-modal.component.scss')],
  templateUrl: './permisotaxiestados-add-modal.component.html'
})
export class PermisotaxiestadosAddModalComponent extends DialogComponent<PermisotaxiestadosInterface, any> implements OnInit, PermisotaxiestadosInterface {
  _permisotaxi: string[] = [];
  _estadoactividad: string[] = [];
permisotaxiRewriteable: boolean = false;
estadoactividadRewriteable: boolean = false;

  permisotaxi_idpermisotaxi: number;
  estadoactividad_idestadoactividad: number;
  fecha: string;
  hora: string;

  modalHeader: string;
  data: any;
  form: FormGroup;
  submitted: boolean = false;
  permisotaxi_idpermisotaxiAC: AbstractControl;
  estadoactividad_idestadoactividadAC: AbstractControl;
  fechaAC: AbstractControl;
  horaAC: AbstractControl;

  constructor(
    private service: PermisotaxiestadosService,
    private authService: AuthService, 
    private permisotaxisService: PermisotaxisService,
    private estadoactividadsService: EstadoactividadsService,
    fb: FormBuilder,
    private toastrService: ToastrService,
    private authLocalstorage: AuthLocalstorage,
    dialogService: DialogService
  ) {
    super(dialogService);
    this.form = fb.group({
    'permisotaxi_idpermisotaxiAC' : ['', Validators.compose([ Validators.required, Validators.maxLength(11)])],
    'estadoactividad_idestadoactividadAC' : ['', Validators.compose([ Validators.required, Validators.maxLength(11)])],
    'fechaAC' : [''],
    'horaAC' : [''],
    });
    this.permisotaxi_idpermisotaxiAC = this.form.controls['permisotaxi_idpermisotaxiAC'];
    this.estadoactividad_idestadoactividadAC = this.form.controls['estadoactividad_idestadoactividadAC'];
    this.fechaAC = this.form.controls['fechaAC'];
    this.horaAC = this.form.controls['horaAC'];
    // Buscar permisos del usuario en el mdulo
    const user = this.authService.useJwtHelper();

    if (user.super) {
        this.permisotaxiRewriteable = true;
        this.estadoactividadRewriteable = true;
    } else {
        const userModules = this.authService.getUserModules();
        if (userModules[0]) {
            for (const element in userModules) {
                if (userModules.hasOwnProperty(element)) {
                    if (userModules[element].path === '/pages/permisotaxis') {
                        this.permisotaxiRewriteable = userModules[element].writeable;
                    }
                    if (userModules[element].path === '/pages/estadoactividads') {
                        this.estadoactividadRewriteable = userModules[element].writeable;
                    }
                } 
            }
        }
    }
  }
  ngOnInit() {
      this.getPermisotaxi();
      this.getEstadoactividad();
      
    // FECHA Y HORA ACTUAL
    const date = this.authLocalstorage.getCurrentDateAndHour();
    this.fecha = date.fecha;
    this.hora = date.hora;
  }
  permisotaxiAddModalShow() {
      const disposable = this.dialogService.addDialog(PermisotaxisAddModalComponent)
      .subscribe( data => {
          if (data) {
              this.permisotaxiShowToast(data);
          }
      }); 
  }
  permisotaxiShowToast(result) {
      if (result.success) {
          this.toastrService.success(result.message);
          this.getPermisotaxi();
      } else {
          this.toastrService.error(result.message);
      }
  }
  estadoactividadAddModalShow() {
      const disposable = this.dialogService.addDialog(EstadoactividadsAddModalComponent)
      .subscribe( data => {
          if (data) {
              this.estadoactividadShowToast(data);
          }
      }); 
  }
  estadoactividadShowToast(result) {
      if (result.success) {
          this.toastrService.success(result.message);
          this.getEstadoactividad();
      } else {
          this.toastrService.error(result.message);
      }
  }
  getPermisotaxi() {
      this.permisotaxisService.all()
      .subscribe(
          (data: any) => this._permisotaxi = data.result,
      );
  }
  getEstadoactividad() {
      this.estadoactividadsService.all()
      .subscribe(
          (data: any) => this._estadoactividad = data.result,
      );
  }
  confirm() {
    this.result = this.data;
    this.close();
  }
  onSubmit(values: PermisotaxiestadosInterface): void {
    this.submitted = true;
    if (this.form.valid) {
      this.service
        .insert({
                  permisotaxi_idpermisotaxi: this.permisotaxi_idpermisotaxi,
                  estadoactividad_idestadoactividad: this.estadoactividad_idestadoactividad,
                  fecha: this.fecha,
                  hora: this.hora,
        })
        .subscribe(
            (data: any) => {
              this.data = data;
              this.confirm();
            });
    }
  }
}



