import * as _ from 'lodash';
import { Pipe, PipeTransform } from '@angular/core';
@Pipe({
    name: 'vehiculotaller_vehiculotaller_idvehiculotallerDataFilter'
})
export class Vehiculotaller_vehiculotaller_idvehiculotallerFilterPipe implements PipeTransform {
    transform(array: any[], query: string): any {
        if (query) {
            return _.filter(array, row => row.vehiculotaller_vehiculotaller_idvehiculotaller.indexOf(query) > -1);
        }
        return array;
    }
}



