import { AuthService } from './../../../../shared/auth.service';
import { VelizsResponseInterface } from './velizs-response.interface';
import { Observable } from 'rxjs/Observable';
import { VelizsInterface } from './velizs.interface';
import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Configuration } from '../../../../app.constants';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Injectable()
export class VelizsService {
    private actionUrl: string;
    private headers: Headers;
    private options: RequestOptions;
    private endPoint: string;
    constructor(
        private _http: Http,
        private _configuration: Configuration,
        private authService: AuthService) {
        this.headers = new Headers();
        this.headers.append('Content-Type', 'application/json; charset=UTF-8');
        this.headers.append('Authorization', 'JWT ' + this.authService.token);
        this.options = new RequestOptions({ headers: this.headers });
        this.endPoint = `${this._configuration.ServerWithApiUrl}veliz`;
       }
       
       allFromTo = ( fechaDesde: string, fechaHasta: string ) : Observable<VelizsResponseInterface> => {
           return this._http.get(`${this.endPoint}/from-to/${fechaDesde}/${fechaHasta}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       // DEVUELVE MONTO ADEUDADO TOTAL DE LIQUIDACIONES POR CHOFER
       adeudoFromIdChofer = ( id ) : Observable<VelizsResponseInterface> => {
           return this._http.get(`${this.endPoint}/adeudo-from-idchofer/${id}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       velizFromIdchoferFecha = ( data ) : Observable<VelizsResponseInterface> => {
           // MODIFICO LA FECHA PARA REEMPLAZAR - POR _ Y NO FALLE AL LLAMAR ENDPOINT, EN ENDPOINT VUELVO A ARMAR CON -
           const newFecha = data.fecha.split('-');
           const fecha = newFecha[0] + "_" + newFecha[1] + "_" + newFecha[2];
           return this._http.get(`${this.endPoint}/veliz-from-idchofer/${data.chofer_idchofer}/fecha/${fecha}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       allAdeudandoFromIdChofer = ( id ) : Observable<VelizsResponseInterface> => {
           return this._http.get(`${this.endPoint}/adeudando-from-idchofer/${id}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }

       findByIdChofer = ( id ) : Observable<VelizsResponseInterface> => {
           return this._http.get(`${this.endPoint}/chofer/${id}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       findByIdEstado = ( id ) : Observable<VelizsResponseInterface> => {
           return this._http.get(`${this.endPoint}/estado/${id}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       all = () : Observable<VelizsResponseInterface> => {
           return this._http.get(this.endPoint, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       findById = ( id ) : Observable<VelizsResponseInterface> => {
           return this._http.get(`${this.endPoint}/${id}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       update = ( veliz: VelizsInterface ) : Observable<VelizsResponseInterface> => {
           return this._http.patch(this.endPoint, veliz, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       remove= ( id ) : Observable<VelizsResponseInterface> => {
           return this._http.delete(`${this.endPoint}/${id}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       exist = ( id ) : Observable<VelizsResponseInterface> => {
           return this._http.get(`${this.endPoint}/${id}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       count = () : Observable<VelizsResponseInterface> => {
           return this._http.get(`${this.endPoint}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       insertWithIdpermisotaxi = ( veliz: VelizsInterface, idpermisotaxi: number, element: number ) : Observable<VelizsResponseInterface> => {
           return this._http.post(`${this.endPoint}/idpermisotaxi`, { 'veliz': veliz, 'idpermisotaxi': idpermisotaxi, 'element': element }, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       insert = ( veliz: VelizsInterface) : Observable<VelizsResponseInterface> => {
           return this._http.post(this.endPoint, veliz, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       private handleError(error: Response) {
        console.error(error);
        return Observable.throw(error.json().error || 'Server error');
    }
}



