import { AuthService } from './../../../../shared/auth.service';
import { VehiculotallersResponseInterface } from './vehiculotallers-response.interface';
import { VehiculotallersInterface } from './vehiculotallers.interface';
import { Observable } from 'rxjs/Observable';
import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Configuration } from '../../../../app.constants';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Injectable()
export class VehiculotallersService {
    private actionUrl: string;
    private headers: Headers;
    private options: RequestOptions;
    private endPoint: string;
    constructor(
        private _http: Http,
        private _configuration: Configuration,
        private authService: AuthService) {
        this.headers = new Headers();
        this.headers.append('Content-Type', 'application/json; charset=UTF-8');
        this.headers.append('Authorization', 'JWT ' + this.authService.token);
        this.options = new RequestOptions({ headers: this.headers });
        this.endPoint = `${this._configuration.ServerWithApiUrl}vehiculotaller`;
       }

       goOutVehicle = ( vehiculoreparando: VehiculotallersInterface ) : Observable<VehiculotallersResponseInterface> => {
           return this._http.post(`${this.endPoint}/go-out-vehicle`, vehiculoreparando, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }

       findByIdEmpleado = ( id ) : Observable<VehiculotallersResponseInterface> => {
           return this._http.get(`${this.endPoint}/empleado/${id}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       findByIdEstado = ( id ) : Observable<VehiculotallersResponseInterface> => {
           return this._http.get(`${this.endPoint}/estado/${id}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       findByIdTaller = ( id ) : Observable<VehiculotallersResponseInterface> => {
           return this._http.get(`${this.endPoint}/taller/${id}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       findByIdVehiculo = ( id ) : Observable<VehiculotallersResponseInterface> => {
           return this._http.get(`${this.endPoint}/vehiculo/${id}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       all = () : Observable<VehiculotallersResponseInterface> => {
           return this._http.get(this.endPoint, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       findById = ( id ) : Observable<VehiculotallersResponseInterface> => {
           return this._http.get(`${this.endPoint}/${id}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       update = ( vehiculotaller: VehiculotallersInterface ) : Observable<VehiculotallersResponseInterface> => {
           return this._http.patch(this.endPoint, vehiculotaller, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       remove= ( id ) : Observable<VehiculotallersResponseInterface> => {
           return this._http.delete(`${this.endPoint}/${id}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       exist = ( id ) : Observable<VehiculotallersResponseInterface> => {
           return this._http.get(`${this.endPoint}/${id}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       count = () : Observable<VehiculotallersResponseInterface> => {
           return this._http.get(`${this.endPoint}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       insert = ( vehiculotaller: VehiculotallersInterface ) : Observable<VehiculotallersResponseInterface> => {
           return this._http.post(this.endPoint, vehiculotaller, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       private handleError(error: Response) {
        console.error(error);
        return Observable.throw(error.json().error || 'Server error');
    }
}



