import { AuthService } from './../../../../shared/auth.service';
import { PagoliquidacionsResponseInterface } from './pagoliquidacions-response.interface';
import { Observable } from 'rxjs/Observable';
import { PagoliquidacionsInterface } from './pagoliquidacions.interface';
import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Configuration } from '../../../../app.constants';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Injectable()
export class PagoliquidacionsService {
    private actionUrl: string;
    private headers: Headers;
    private options: RequestOptions;
    private endPoint: string;
    constructor(
        private _http: Http,
        private _configuration: Configuration,
        private authService: AuthService) {
        this.headers = new Headers();
        this.headers.append('Content-Type', 'application/json; charset=UTF-8');
        this.headers.append('Authorization', 'JWT ' + this.authService.token);
        this.options = new RequestOptions({ headers: this.headers });
        this.endPoint = `${this._configuration.ServerWithApiUrl}pagoliquidacion`;
       }

       pagoLiquidaciones = (pagoliquidacion: string[], 
                            totalLiquidacion: number, 
                            idpago: number, 
                            idchofer: number ) : Observable<PagoliquidacionsResponseInterface> => {

           const pagoliquidaciones = {
               'pagoliquidacion': pagoliquidacion,
               'totalLiquidacion': totalLiquidacion, 
               'idpago': idpago,
               'idchofer': idchofer};

           return this._http.post(`${this.endPoint}/pago-liquidaciones`, pagoliquidaciones, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }

       findByIdChofer = ( id ) : Observable<PagoliquidacionsResponseInterface> => {
           return this._http.get(`${this.endPoint}/chofer/${id}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       findByIdPago = ( id ) : Observable<PagoliquidacionsResponseInterface> => {
           return this._http.get(`${this.endPoint}/pago/${id}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       all = () : Observable<PagoliquidacionsResponseInterface> => {
           return this._http.get(this.endPoint, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       findById = ( id ) : Observable<PagoliquidacionsResponseInterface> => {
           return this._http.get(`${this.endPoint}/${id}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       update = ( pagoliquidacion: PagoliquidacionsInterface ) : Observable<PagoliquidacionsResponseInterface> => {
           return this._http.patch(this.endPoint, pagoliquidacion, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       remove= ( id ) : Observable<PagoliquidacionsResponseInterface> => {
           return this._http.delete(`${this.endPoint}/${id}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       exist = ( id ) : Observable<PagoliquidacionsResponseInterface> => {
           return this._http.get(`${this.endPoint}/${id}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       count = () : Observable<PagoliquidacionsResponseInterface> => {
           return this._http.get(`${this.endPoint}`, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       insert = ( pagoliquidacion: PagoliquidacionsInterface ) : Observable<PagoliquidacionsResponseInterface> => {
           return this._http.post(this.endPoint, pagoliquidacion, this.options)
               .map((response: Response) => response.json())
               .catch(this.handleError);
       }
       private handleError(error: Response) {
        console.error(error);
        return Observable.throw(error.json().error || 'Server error');
    }
}



