import { AuthService } from './../../../../../shared/auth.service';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { AuthLocalstorage } from './../../../../../shared/auth-localstorage.service';
import { VehiculotallersService } from './../vehiculotallers.service';
import { VehiculotallersInterface } from './../vehiculotallers.interface';
import { Component, OnInit } from '@angular/core';
import { FormGroup, AbstractControl, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { EstadosService } from './../../../../estados/components/estados-table/estados.service';
import { EstadosAddModalComponent } from './../../../../estados/components/estados-table/estados-add-modal/estados-add-modal.component';
import { TallersService } from './../../../../tallers/components/tallers-table/tallers.service';
import { TallersAddModalComponent } from './../../../../tallers/components/tallers-table/tallers-add-modal/tallers-add-modal.component';
import { EmpleadosService } from './../../../../empleados/components/empleados-table/empleados.service';
import { EmpleadosAddModalComponent } from './../../../../empleados/components/empleados-table/empleados-add-modal/empleados-add-modal.component';
import { VehiculosService } from './../../../../vehiculos/components/vehiculos-table/vehiculos.service';
import { VehiculosAddModalComponent } from './../../../../vehiculos/components/vehiculos-table/vehiculos-add-modal/vehiculos-add-modal.component';
@Component({
  selector: 'edit-service-modal',
  styleUrls: [('./vehiculotallers-edit-modal.component.scss')],
  templateUrl: './vehiculotallers-edit-modal.component.html'
})
export class VehiculotallersEditModalComponent extends DialogComponent<VehiculotallersInterface, any> implements OnInit, VehiculotallersInterface {
  _estado: string[] = [];
  _taller: string[] = [];
  _empleado: string[] = [];
  _vehiculo: string[] = [];

  idvehiculotaller: number;
  fechaIngresa: string;
  horaIngresa: string;
  fechaSalida: string;
  horaSalida: string;
  fechaEstimada: string;
  horaEstimada: string;
  inventario: string;
  motivo: string;
  estado_idestado: number;
  taller_idtaller: number;
  empleado_idempleado: number;
  vehiculo_idvehiculo: number;
  kilometraje: number;

estadoRewriteable: boolean = false;
tallerRewriteable: boolean = false;
empleadoRewriteable: boolean = false;
vehiculoRewriteable: boolean = false;

  modalHeader: string;
  data: any;
  form: FormGroup;
  submitted: boolean = false;

  fechaIngresaAC: AbstractControl;
  horaIngresaAC: AbstractControl;
  fechaSalidaAC: AbstractControl;
  horaSalidaAC: AbstractControl;
  fechaEstimadaAC: AbstractControl;
  horaEstimadaAC: AbstractControl;
  inventarioAC: AbstractControl;
  motivoAC: AbstractControl;
  estado_idestadoAC: AbstractControl;
  taller_idtallerAC: AbstractControl;
  empleado_idempleadoAC: AbstractControl;
  vehiculo_idvehiculoAC: AbstractControl;
  kilometrajeAC: AbstractControl;

  constructor(
      private service: VehiculotallersService,
      private authService: AuthService, 
      private estadosService: EstadosService,
      private tallersService: TallersService,
      private empleadosService: EmpleadosService,
      private vehiculosService: VehiculosService,
      fb: FormBuilder,
      private toastrService: ToastrService,
      private authLocalstorage: AuthLocalstorage,
      dialogService: DialogService,
  ) {
  super(dialogService);
  this.form = fb.group({
    'fechaIngresaAC' : [''],
    'horaIngresaAC' : [''],
    'fechaSalidaAC' : [''],
    'horaSalidaAC' : [''],
    'fechaEstimadaAC' : [''],
    'horaEstimadaAC' : [''],
    'inventarioAC' : ['',Validators.compose([Validators.maxLength(80)])],
    'motivoAC' : ['',Validators.compose([Validators.maxLength(80)])],
    'estado_idestadoAC' : ['',Validators.compose([Validators.required,Validators.maxLength(3)])],
    'taller_idtallerAC' : ['',Validators.compose([Validators.required,Validators.maxLength(11)])],
    'empleado_idempleadoAC' : ['',Validators.compose([Validators.required,Validators.maxLength(11)])],
    'vehiculo_idvehiculoAC' : ['',Validators.compose([Validators.maxLength(11)])],
    'kilometrajeAC' : ['',Validators.compose([Validators.maxLength(11)])],
  });
  this.fechaIngresaAC = this.form.controls['fechaIngresaAC'];
  this.horaIngresaAC = this.form.controls['horaIngresaAC'];
  this.fechaSalidaAC = this.form.controls['fechaSalidaAC'];
  this.horaSalidaAC = this.form.controls['horaSalidaAC'];
  this.fechaEstimadaAC = this.form.controls['fechaEstimadaAC'];
  this.horaEstimadaAC = this.form.controls['horaEstimadaAC'];
  this.inventarioAC = this.form.controls['inventarioAC'];
  this.motivoAC = this.form.controls['motivoAC'];
  this.estado_idestadoAC = this.form.controls['estado_idestadoAC'];
  this.taller_idtallerAC = this.form.controls['taller_idtallerAC'];
  this.empleado_idempleadoAC = this.form.controls['empleado_idempleadoAC'];
  this.vehiculo_idvehiculoAC = this.form.controls['vehiculo_idvehiculoAC'];
  this.kilometrajeAC = this.form.controls['kilometrajeAC'];
    // Buscar permisos del usuario en el mdulo
    const user = this.authService.useJwtHelper();

    if (user.super) {
        this.estadoRewriteable = true;
        this.tallerRewriteable = true;
        this.empleadoRewriteable = true;
        this.vehiculoRewriteable = true;
    } else {
        const userModules = this.authService.getUserModules();
        if (userModules[0]) {
            for (const element in userModules) {
                if (userModules.hasOwnProperty(element)) {
                    if (userModules[element].path === '/pages/estados') {
                        this.estadoRewriteable = userModules[element].writeable;
                    }
                    if (userModules[element].path === '/pages/tallers') {
                        this.tallerRewriteable = userModules[element].writeable;
                    }
                    if (userModules[element].path === '/pages/empleados') {
                        this.empleadoRewriteable = userModules[element].writeable;
                    }
                    if (userModules[element].path === '/pages/vehiculos') {
                        this.vehiculoRewriteable = userModules[element].writeable;
                    }
                } 
            }
        }
    }
  }
  ngOnInit() {
      this.getEstado();
      this.getTaller();
      this.getEmpleado();
      this.getVehiculo();
  }
  estadoAddModalShow() {
      const disposable = this.dialogService.addDialog(EstadosAddModalComponent)
      .subscribe( data => {
          if (data) {
              this.estadoShowToast(data);
          }
      }); 
  }
  estadoShowToast(result) {
      if (result.success) {
          this.toastrService.success(result.message);
          this.getEstado();
      } else {
          this.toastrService.error(result.message);
      }
  }
  tallerAddModalShow() {
      const disposable = this.dialogService.addDialog(TallersAddModalComponent)
      .subscribe( data => {
          if (data) {
              this.tallerShowToast(data);
          }
      }); 
  }
  tallerShowToast(result) {
      if (result.success) {
          this.toastrService.success(result.message);
          this.getTaller();
      } else {
          this.toastrService.error(result.message);
      }
  }
  empleadoAddModalShow() {
      const disposable = this.dialogService.addDialog(EmpleadosAddModalComponent)
      .subscribe( data => {
          if (data) {
              this.empleadoShowToast(data);
          }
      }); 
  }
  empleadoShowToast(result) {
      if (result.success) {
          this.toastrService.success(result.message);
          this.getEmpleado();
      } else {
          this.toastrService.error(result.message);
      }
  }
  vehiculoAddModalShow() {
      const disposable = this.dialogService.addDialog(VehiculosAddModalComponent)
      .subscribe( data => {
          if (data) {
              this.vehiculoShowToast(data);
          }
      }); 
  }
  vehiculoShowToast(result) {
      if (result.success) {
          this.toastrService.success(result.message);
          this.getVehiculo();
      } else {
          this.toastrService.error(result.message);
      }
  }
  getEstado() {
      this.estadosService.all()
      .subscribe(
          (data: any) => this._estado = data.result,
      );
  }
  getTaller() {
      this.tallersService.all()
      .subscribe(
          (data: any) => this._taller = data.result,
      );
  }
  getEmpleado() {
      this.empleadosService.all()
      .subscribe(
          (data: any) => this._empleado = data.result,
      );
  }
  getVehiculo() {
      this.vehiculosService.all()
      .subscribe(
          (data: any) => this._vehiculo = data.result,
      );
  }
  confirm() {
      this.result = this.data;
      this.close();
  }
  onSubmit(values: VehiculotallersInterface): void {
      this.submitted = true;
      if (this.form.valid) {
          this.service
              .update({
                  idvehiculotaller: this.idvehiculotaller,
                  fechaIngresa: this.fechaIngresa,
                  horaIngresa: this.horaIngresa,
                  fechaSalida: this.fechaSalida,
                  horaSalida: this.horaSalida,
                  fechaEstimada: this.fechaEstimada,
                  horaEstimada: this.horaEstimada,
                  inventario: this.inventario,
                  motivo: this.motivo,
                  estado_idestado: this.estado_idestado,
                  taller_idtaller: this.taller_idtaller,
                  empleado_idempleado: this.empleado_idempleado,
                  vehiculo_idvehiculo: this.vehiculo_idvehiculo,
                  kilometraje: this.kilometraje,
              })
              .subscribe(
                  (data: any) => {
                      this.data = data;
                      this.confirm();
              });
          }
  }
}



