import { ChofersInterface } from './../../../../chofers/components/chofers-table/chofers.interface';
import { ChofersResponseInterface } from './../../../../chofers/components/chofers-table/chofers-response.interface';
import { AuthService } from './../../../../../shared/auth.service';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { AuthLocalstorage } from './../../../../../shared/auth-localstorage.service';
import { PagofianzasService } from './../pagofianzas.service';
import { PagofianzasInterface } from './../pagofianzas.interface';
import { Component, OnInit } from '@angular/core';
import { FormGroup, AbstractControl, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { PagosService } from './../../../../pagos/components/pagos-table/pagos.service';
import { PagosAddModalComponent } from './../../../../pagos/components/pagos-table/pagos-add-modal/pagos-add-modal.component';
import { ChofersService } from './../../../../chofers/components/chofers-table/chofers.service';
import { ChofersAddModalComponent } from './../../../../chofers/components/chofers-table/chofers-add-modal/chofers-add-modal.component';

@Component({
  selector: 'add-service-modal',
  styleUrls: [('./pagofianzas-add-modal.component.scss')],
  templateUrl: './pagofianzas-add-modal.component.html'
})
export class PagofianzasAddModalComponent extends DialogComponent<PagofianzasInterface, any> implements OnInit, PagofianzasInterface {
  _pago: string[] = [];
  _chofer: string[] = [];
pagoRewriteable: boolean = false;
choferRewriteable: boolean = false;

  idpagofianza: number;
  montopagado: number;
  saldoactual: number;
  chofer_idchofer: number;

  modalHeader: string;
  data: any;
  form: FormGroup;
  submitted: boolean = false;
  idpagofianzaAC: AbstractControl;
  montopagadoAC: AbstractControl;
  chofer_idchoferAC: AbstractControl;

  constructor(
    private service: PagofianzasService,
    private authService: AuthService, 
    private pagosService: PagosService,
    private chofersService: ChofersService,
    fb: FormBuilder,
    private toastrService: ToastrService,
    private authLocalstorage: AuthLocalstorage,
    dialogService: DialogService
  ) {
    super(dialogService);
    this.form = fb.group({
    'idpagofianzaAC' : [''],
    'montopagadoAC' : [''],
    'chofer_idchoferAC' : ['', Validators.compose([ Validators.required, Validators.maxLength(11)])],
    });
    this.idpagofianzaAC = this.form.controls['idpagofianzaAC'];
    this.montopagadoAC = this.form.controls['montopagadoAC'];
    this.chofer_idchoferAC = this.form.controls['chofer_idchoferAC'];
    // Buscar permisos del usuario en el mdulo
    const user = this.authService.useJwtHelper();

    if (user.super) {
        this.pagoRewriteable = true;
        this.choferRewriteable = true;
    } else {
        const userModules = this.authService.getUserModules();
        if (userModules[0]) {
            for (const element in userModules) {
                if (userModules.hasOwnProperty(element)) {
                    if (userModules[element].path === '/pages/pagos') {
                        this.pagoRewriteable = userModules[element].writeable;
                    }
                    if (userModules[element].path === '/pages/chofers') {
                        this.choferRewriteable = userModules[element].writeable;
                    }
                } 
            }
        }
    }
  }
  ngOnInit() {
      this.getPago();
      this.getChofer();
  }
  pagoAddModalShow() {
      const disposable = this.dialogService.addDialog(PagosAddModalComponent)
      .subscribe( data => {
          if (data) {
              this.pagoShowToast(data);
          }
      }); 
  }
  pagoShowToast(result) {
      if (result.success) {
          this.toastrService.success(result.message);
          this.getPago();
      } else {
          this.toastrService.error(result.message);
      }
  }
  choferAddModalShow() {
      const disposable = this.dialogService.addDialog(ChofersAddModalComponent)
      .subscribe( data => {
          if (data) {
              this.choferShowToast(data);
          }
      }); 
  }
  choferShowToast(result) {
      if (result.success) {
          this.toastrService.success(result.message);
          this.getChofer();
      } else {
          this.toastrService.error(result.message);
      }
  }
  getPago() {
      this.pagosService.all()
      .subscribe(
          (data: any) => this._pago = data.result,
      );
  }
  getChofer() {
      this.chofersService.all()
      .subscribe(
          (data: any) => this._chofer = data.result,
      );
  }
  confirm() {
    this.result = this.data;
    this.close();
  }

  updateChofer(data: ChofersInterface) {
      this.chofersService.update(data)
      .subscribe(
          (result: any) => {
              this.data = result;
              this.confirm();
          });
  }

  onSubmit(values: PagofianzasInterface): void {
    this.submitted = true;
    if (this.form.valid) {
      this.service
        .insert({
                  idpagofianza: this.idpagofianza,
                  montopagado: this.montopagado,
                  chofer_idchofer: this.chofer_idchofer,
        })
        .subscribe(
            (data: any) => {

                if (data.success) {
                    
                        this.chofersService.findById(this.chofer_idchofer)
                        .subscribe(
                            (chofer: ChofersResponseInterface) => {
                                if (chofer.success) {
                                    
                                    const montoPagadoFianza = chofer.result[0].deudafianza + this.montopagado;  // DEUDA FIANZA ES LO QUE YA LLEVA PAGADO
                                    const fianza = chofer.result[0].fianza;  // EL TOTAL A PAGAR

                                    let idestado = 0;
                                    if (fianza <= montoPagadoFianza) {
                                        idestado = 8; // Pagado
                                    } else {
                                        idestado = 9; // Adeudando
                                    }    
                                    this.updateChofer({
                                        'idchofer': this.chofer_idchofer,
                                        'deudafianza': montoPagadoFianza,
                                        'estado_idestado_fianza': idestado,
                                    });
                                }
                            });
                }

              this.data = data;
              this.confirm();
            });
    }
  }
}



