import { AuthService } from './../../../../../shared/auth.service';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { AuthLocalstorage } from './../../../../../shared/auth-localstorage.service';
import { OrdenestadosService } from './../ordenestados.service';
import { OrdenestadosInterface } from './../ordenestados.interface';
import { Component, OnInit } from '@angular/core';
import { FormGroup, AbstractControl, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { OrdensService } from './../../../../ordens/components/ordens-table/ordens.service';
import { OrdensAddModalComponent } from './../../../../ordens/components/ordens-table/ordens-add-modal/ordens-add-modal.component';
import { EstadopagosService } from './../../../../estadopagos/components/estadopagos-table/estadopagos.service';
import { EstadopagosAddModalComponent } from './../../../../estadopagos/components/estadopagos-table/estadopagos-add-modal/estadopagos-add-modal.component';

@Component({
  selector: 'add-service-modal',
  styleUrls: [('./ordenestados-add-modal.component.scss')],
  templateUrl: './ordenestados-add-modal.component.html'
})
export class OrdenestadosAddModalComponent extends DialogComponent<OrdenestadosInterface, any> implements OnInit, OrdenestadosInterface {
  _orden: string[] = [];
  _estadopago: string[] = [];
ordenRewriteable: boolean = false;
estadopagoRewriteable: boolean = false;

  orden_idorden: number;
  estadopago_idestadopago: number;
  fecha: string;
  hora: string;

  modalHeader: string;
  data: any;
  form: FormGroup;
  submitted: boolean = false;
  orden_idordenAC: AbstractControl;
  estadopago_idestadopagoAC: AbstractControl;
  fechaAC: AbstractControl;
  horaAC: AbstractControl;

  constructor(
    private service: OrdenestadosService,
    private authService: AuthService, 
    private ordensService: OrdensService,
    private estadopagosService: EstadopagosService,
    fb: FormBuilder,
    private toastrService: ToastrService,
    private authLocalstorage: AuthLocalstorage,
    dialogService: DialogService
  ) {
    super(dialogService);
    this.form = fb.group({
    'orden_idordenAC' : ['', Validators.compose([ Validators.required, Validators.maxLength(11)])],
    'estadopago_idestadopagoAC' : ['', Validators.compose([ Validators.required, Validators.maxLength(11)])],
    'fechaAC' : [''],
    'horaAC' : [''],
    });
    this.orden_idordenAC = this.form.controls['orden_idordenAC'];
    this.estadopago_idestadopagoAC = this.form.controls['estadopago_idestadopagoAC'];
    this.fechaAC = this.form.controls['fechaAC'];
    this.horaAC = this.form.controls['horaAC'];
    // Buscar permisos del usuario en el mdulo
    const user = this.authService.useJwtHelper();

    if (user.super) {
        this.ordenRewriteable = true;
        this.estadopagoRewriteable = true;
    } else {
        const userModules = this.authService.getUserModules();
        if (userModules[0]) {
            for (const element in userModules) {
                if (userModules.hasOwnProperty(element)) {
                    if (userModules[element].path === '/pages/ordens') {
                        this.ordenRewriteable = userModules[element].writeable;
                    }
                    if (userModules[element].path === '/pages/estadopagos') {
                        this.estadopagoRewriteable = userModules[element].writeable;
                    }
                } 
            }
        }
    }
  }
  ngOnInit() {
      this.getOrden();
      this.getEstadopago();

    // FECHA Y HORA ACTUAL
    const date = this.authLocalstorage.getCurrentDateAndHour();
    this.fecha = date.fecha;
    this.hora = date.hora;
  }
  ordenAddModalShow() {
      const disposable = this.dialogService.addDialog(OrdensAddModalComponent)
      .subscribe( data => {
          if (data) {
              this.ordenShowToast(data);
          }
      }); 
  }
  ordenShowToast(result) {
      if (result.success) {
          this.toastrService.success(result.message);
          this.getOrden();
      } else {
          this.toastrService.error(result.message);
      }
  }
  estadopagoAddModalShow() {
      const disposable = this.dialogService.addDialog(EstadopagosAddModalComponent)
      .subscribe( data => {
          if (data) {
              this.estadopagoShowToast(data);
          }
      }); 
  }
  estadopagoShowToast(result) {
      if (result.success) {
          this.toastrService.success(result.message);
          this.getEstadopago();
      } else {
          this.toastrService.error(result.message);
      }
  }
  getOrden() {
      this.ordensService.all()
      .subscribe(
          (data: any) => this._orden = data.result,
      );
  }
  getEstadopago() {
      this.estadopagosService.all()
      .subscribe(
          (data: any) => this._estadopago = data.result,
      );
  }
  confirm() {
    this.result = this.data;
    this.close();
  }
  onSubmit(values: OrdenestadosInterface): void {
    this.submitted = true;
    if (this.form.valid) {
      this.service
        .insert({
                  orden_idorden: this.orden_idorden,
                  estadopago_idestadopago: this.estadopago_idestadopago,
                  fecha: this.fecha,
                  hora: this.hora,
        })
        .subscribe(
            (data: any) => {
              this.data = data;
              this.confirm();
            });
    }
  }
}



