import * as _ from 'lodash';
import { Pipe, PipeTransform } from '@angular/core';
@Pipe({
    name: 'estadoscrum_estadoscrum_idestadoscrumDataFilter'
})
export class Estadoscrum_estadoscrum_idestadoscrumFilterPipe implements PipeTransform {
    transform(array: any[], query: string): any {
        if (query) {
            return _.filter(array, row => row.estadoscrum_estadoscrum_idestadoscrum.indexOf(query) > -1);
        }
        return array;
    }
}



