import { PermisotaxisService } from './../../../../permisotaxis/components/permisotaxis-table/permisotaxis.service';
import { AuthService } from './../../../../../shared/auth.service';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { AuthLocalstorage } from './../../../../../shared/auth-localstorage.service';
import { CorralonsService } from './../corralons.service';
import { CorralonsInterface } from './../corralons.interface';
import { Component, OnInit } from '@angular/core';
import { FormGroup, AbstractControl, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { EstadosService } from './../../../../estados/components/estados-table/estados.service';
import { EstadosAddModalComponent } from './../../../../estados/components/estados-table/estados-add-modal/estados-add-modal.component';
@Component({
  selector: 'edit-service-modal',
  styleUrls: [('./corralons-edit-modal.component.scss')],
  templateUrl: './corralons-edit-modal.component.html'
})
export class CorralonsEditModalComponent extends DialogComponent<CorralonsInterface, any> implements OnInit, CorralonsInterface {
  _estado: string[] = [];
  _permisotaxi: string[] = [];

  idcorralon: number;
  fecha: string;
  hora: string;
  fechaSalida: string;
  horaSalida: string;
  infraccionNumero: number;
  corralonNombre: string;
  motivo: string;
  estado_idestado: number;
  permisotaxi_idpermisotaxi: number;


estadoRewriteable: boolean = false;

  modalHeader: string;
  data: any;
  form: FormGroup;
  submitted: boolean = false;

  fechaAC: AbstractControl;
  horaAC: AbstractControl;
  fechaSalidaAC: AbstractControl;
  horaSalidaAC: AbstractControl;
  infraccionNumeroAC: AbstractControl;
  corralonNombreAC: AbstractControl;
  motivoAC: AbstractControl;
  estado_idestadoAC: AbstractControl;
  permisotaxi_idpermisotaxiAC: AbstractControl;
  
  constructor(
      private service: CorralonsService,
      private authService: AuthService, 
      private estadosService: EstadosService,
      fb: FormBuilder,
      private toastrService: ToastrService,
      private authLocalstorage: AuthLocalstorage,
      private permisotaxisService: PermisotaxisService,
      dialogService: DialogService,
  ) {
  super(dialogService);
  this.form = fb.group({
    'fechaAC' : [''],
    'horaAC' : [''],
    'fechaSalidaAC' : [''],
    'horaSalidaAC' : [''],
    'infraccionNumeroAC' : ['',Validators.compose([Validators.maxLength(11)])],
    'corralonNombreAC' : ['',Validators.compose([Validators.maxLength(45)])],
    'motivoAC' : ['',Validators.compose([Validators.maxLength(150)])],
    'estado_idestadoAC' : ['',Validators.compose([Validators.required,Validators.maxLength(3)])],
    'permisotaxi_idpermisotaxiAC' : ['',Validators.compose([Validators.required,Validators.maxLength(11)])],
  });
  this.fechaAC = this.form.controls['fechaAC'];
  this.horaAC = this.form.controls['horaAC'];
  this.fechaSalidaAC = this.form.controls['fechaSalidaAC'];
  this.horaSalidaAC = this.form.controls['horaSalidaAC'];
  this.infraccionNumeroAC = this.form.controls['infraccionNumeroAC'];
  this.corralonNombreAC = this.form.controls['corralonNombreAC'];
  this.motivoAC = this.form.controls['motivoAC'];
  this.estado_idestadoAC = this.form.controls['estado_idestadoAC'];
  this.permisotaxi_idpermisotaxiAC = this.form.controls['permisotaxi_idpermisotaxiAC'];

    // Buscar permisos del usuario en el mdulo
    const user = this.authService.useJwtHelper();

    if (user.super) {
        this.estadoRewriteable = true;
    } else {
        const userModules = this.authService.getUserModules();
        if (userModules[0]) {
            for (const element in userModules) {
                if (userModules.hasOwnProperty(element)) {
                    if (userModules[element].path === '/pages/estados') {
                        this.estadoRewriteable = userModules[element].writeable;
                    }
                } 
            }
        }
    }
  }
  ngOnInit() {
      this.getEstado();
      this.getPermisotaxi();

      // FECHA Y HORA ACTUAL
      const date = this.authLocalstorage.getCurrentDateAndHour();
      this.fechaSalida = date.fecha;
      this.horaSalida = date.hora;
  }

  estadoAddModalShow() {
      const disposable = this.dialogService.addDialog(EstadosAddModalComponent)
      .subscribe( data => {
          if (data) {
              this.estadoShowToast(data);
          }
      }); 
  }

  estadoShowToast(result) {
      if (result.success) {
          this.toastrService.success(result.message);
          this.getEstado();
      } else {
          this.toastrService.error(result.message);
      }
  }
  getEstado() {
      this.estadosService.all()
      .subscribe(
          (data: any) => this._estado = data.result,
      );
  }
  getPermisotaxi() {
      this.permisotaxisService.all()
      .subscribe(
          (data: any) => this._permisotaxi = data.result,
      );
  }
  confirm() {
      this.result = this.data;
      this.close();
  }
  onSubmit(values: CorralonsInterface): void {
      this.submitted = true;
      if (this.form.valid) {
          this.service
              .update({
                  idcorralon: this.idcorralon,
                  fecha: this.fecha,
                  hora: this.hora,
                  fechaSalida: this.fechaSalida,
                  horaSalida: this.horaSalida,
                  infraccionNumero: this.infraccionNumero,
                  corralonNombre: this.corralonNombre,
                  motivo: this.motivo,
                  estado_idestado: this.estado_idestado,
                  permisotaxi_idpermisotaxi: this.permisotaxi_idpermisotaxi,
              })
              .subscribe(
                  (data: any) => {
                      this.data = data;
                      this.confirm();
              });
          }
  }
}



