import { AuthService } from './../../../../../shared/auth.service';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { AuthLocalstorage } from './../../../../../shared/auth-localstorage.service';
import { FormulasService } from './../formulas.service';
import { FormulasInterface } from './../formulas.interface';
import { Component, OnInit } from '@angular/core';
import { FormGroup, AbstractControl, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
@Component({
  selector: 'edit-service-modal',
  styleUrls: [('./formulas-edit-modal.component.scss')],
  templateUrl: './formulas-edit-modal.component.html'
})
export class FormulasEditModalComponent extends DialogComponent<FormulasInterface, any> implements OnInit, FormulasInterface {

  idformula: number;
  formula: string;

  modalHeader: string;
  data: any;
  form: FormGroup;
  submitted: boolean = false;

  formulaAC: AbstractControl;
  constructor(
      private service: FormulasService,
      private authService: AuthService, 
      fb: FormBuilder,
      private toastrService: ToastrService,
      private authLocalstorage: AuthLocalstorage,
      dialogService: DialogService,
  ) {
  super(dialogService);
  this.form = fb.group({
    'formulaAC' : ['',Validators.compose([Validators.required,Validators.maxLength(145)])],
  });
  this.formulaAC = this.form.controls['formulaAC'];
    // Buscar permisos del usuario en el mdulo
    const user = this.authService.useJwtHelper();

    if (user.super) {
    } else {
        const userModules = this.authService.getUserModules();
        if (userModules[0]) {
            for (const element in userModules) {
                if (userModules.hasOwnProperty(element)) {
                } 
            }
        }
    }
  }
  ngOnInit() {
  }

  confirm() {
      this.result = this.data;
      this.close();
  }
  onSubmit(values: FormulasInterface): void {
      this.submitted = true;
      if (this.form.valid) {
          this.service
              .update({
                  idformula: this.idformula,
                  formula: this.formula,
              })
              .subscribe(
                  (data: any) => {
                      this.data = data;
                      this.confirm();
              });
          }
  }
}



