import * as _ from 'lodash';
import { Pipe, PipeTransform } from '@angular/core';
@Pipe({
    name: 'estadopago_estadopago_idestadopagoDataFilter'
})
export class Estadopago_estadopago_idestadopagoFilterPipe implements PipeTransform {
    transform(array: any[], query: string): any {
        if (query) {
            return _.filter(array, row => row.estadopago_estadopago_idestadopago.indexOf(query) > -1);
        }
        return array;
    }
}



