import { AuthService } from './../../../../../shared/auth.service';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { AuthLocalstorage } from './../../../../../shared/auth-localstorage.service';
import { VehiculotalleredosService } from './../vehiculotalleredos.service';
import { VehiculotalleredosInterface } from './../vehiculotalleredos.interface';
import { Component, OnInit } from '@angular/core';
import { FormGroup, AbstractControl, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { VehiculotallersService } from './../../../../vehiculotallers/components/vehiculotallers-table/vehiculotallers.service';
import { VehiculotallersAddModalComponent } from './../../../../vehiculotallers/components/vehiculotallers-table/vehiculotallers-add-modal/vehiculotallers-add-modal.component';
import { EstadoscrumsService } from './../../../../estadoscrums/components/estadoscrums-table/estadoscrums.service';
import { EstadoscrumsAddModalComponent } from './../../../../estadoscrums/components/estadoscrums-table/estadoscrums-add-modal/estadoscrums-add-modal.component';
@Component({
  selector: 'edit-service-modal',
  styleUrls: [('./vehiculotalleredos-edit-modal.component.scss')],
  templateUrl: './vehiculotalleredos-edit-modal.component.html'
})
export class VehiculotalleredosEditModalComponent extends DialogComponent<VehiculotalleredosInterface, any> implements OnInit, VehiculotalleredosInterface {
  _vehiculotaller: string[] = [];
  _estadoscrum: string[] = [];

  idvehiculotalleredo: number;
  vehiculotaller_idvehiculotaller: number;
  estadoscrum_idestadoscrum: number;
  fecha: string;
  hora: string;
vehiculotallerRewriteable: boolean = false;
estadoscrumRewriteable: boolean = false;

  modalHeader: string;
  data: any;
  form: FormGroup;
  submitted: boolean = false;

  vehiculotaller_idvehiculotallerAC: AbstractControl;
  estadoscrum_idestadoscrumAC: AbstractControl;
  fechaAC: AbstractControl;
  horaAC: AbstractControl;
  constructor(
      private service: VehiculotalleredosService,
      private authService: AuthService, 
      private vehiculotallersService: VehiculotallersService,
      private estadoscrumsService: EstadoscrumsService,
      fb: FormBuilder,
      private toastrService: ToastrService,
      private authLocalstorage: AuthLocalstorage,
      dialogService: DialogService,
  ) {
  super(dialogService);
  this.form = fb.group({
    'vehiculotaller_idvehiculotallerAC' : ['',Validators.compose([Validators.required,Validators.maxLength(11)])],
    'estadoscrum_idestadoscrumAC' : ['',Validators.compose([Validators.required,Validators.maxLength(11)])],
    'fechaAC' : [''],
    'horaAC' : [''],
  });
  this.vehiculotaller_idvehiculotallerAC = this.form.controls['vehiculotaller_idvehiculotallerAC'];
  this.estadoscrum_idestadoscrumAC = this.form.controls['estadoscrum_idestadoscrumAC'];
  this.fechaAC = this.form.controls['fechaAC'];
  this.horaAC = this.form.controls['horaAC'];
    // Buscar permisos del usuario en el mdulo
    const user = this.authService.useJwtHelper();

    if (user.super) {
        this.vehiculotallerRewriteable = true;
        this.estadoscrumRewriteable = true;
    } else {
        const userModules = this.authService.getUserModules();
        if (userModules[0]) {
            for (const element in userModules) {
                if (userModules.hasOwnProperty(element)) {
                    if (userModules[element].path === '/pages/vehiculotallers') {
                        this.vehiculotallerRewriteable = userModules[element].writeable;
                    }
                    if (userModules[element].path === '/pages/estadoscrums') {
                        this.estadoscrumRewriteable = userModules[element].writeable;
                    }
                } 
            }
        }
    }
  }
  ngOnInit() {
      this.getVehiculotaller();
      this.getEstadoscrum();
  }

  vehiculotallerAddModalShow() {
      const disposable = this.dialogService.addDialog(VehiculotallersAddModalComponent)
      .subscribe( data => {
          if (data) {
              this.vehiculotallerShowToast(data);
          }
      }); 
  }

  vehiculotallerShowToast(result) {
      if (result.success) {
          this.toastrService.success(result.message);
          this.getVehiculotaller();
      } else {
          this.toastrService.error(result.message);
      }
  }
  estadoscrumAddModalShow() {
      const disposable = this.dialogService.addDialog(EstadoscrumsAddModalComponent)
      .subscribe( data => {
          if (data) {
              this.estadoscrumShowToast(data);
          }
      }); 
  }

  estadoscrumShowToast(result) {
      if (result.success) {
          this.toastrService.success(result.message);
          this.getEstadoscrum();
      } else {
          this.toastrService.error(result.message);
      }
  }
  getVehiculotaller() {
      this.vehiculotallersService.all()
      .subscribe(
          (data: any) => this._vehiculotaller = data.result,
      );
  }
  getEstadoscrum() {
      this.estadoscrumsService.all()
      .subscribe(
          (data: any) => this._estadoscrum = data.result,
      );
  }
  confirm() {
      this.result = this.data;
      this.close();
  }
  onSubmit(values: VehiculotalleredosInterface): void {
      this.submitted = true;
      if (this.form.valid) {
          this.service
              .update({
                  idvehiculotalleredo: this.idvehiculotalleredo,
                  vehiculotaller_idvehiculotaller: this.vehiculotaller_idvehiculotaller,
                  estadoscrum_idestadoscrum: this.estadoscrum_idestadoscrum,
                  fecha: this.fecha,
                  hora: this.hora,
              })
              .subscribe(
                  (data: any) => {
                      this.data = data;
                      this.confirm();
              });
          }
  }
}



