import { AuthService } from './../../../../../shared/auth.service';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { AuthLocalstorage } from './../../../../../shared/auth-localstorage.service';
import { OrdensService } from './../ordens.service';
import { OrdensInterface } from './../ordens.interface';
import { Component, OnInit } from '@angular/core';
import { FormGroup, AbstractControl, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { ClientesService } from './../../../../clientes/components/clientes-table/clientes.service';
import { ClientesAddModalComponent } from './../../../../clientes/components/clientes-table/clientes-add-modal/clientes-add-modal.component';
import { VehiculotallersService } from './../../../../vehiculotallers/components/vehiculotallers-table/vehiculotallers.service';
import { VehiculotallersAddModalComponent } from './../../../../vehiculotallers/components/vehiculotallers-table/vehiculotallers-add-modal/vehiculotallers-add-modal.component';
@Component({
  selector: 'edit-service-modal',
  styleUrls: [('./ordens-edit-modal.component.scss')],
  templateUrl: './ordens-edit-modal.component.html'
})
export class OrdensEditModalComponent extends DialogComponent<OrdensInterface, any> implements OnInit, OrdensInterface {
  _cliente: string[] = [];
  _vehiculotaller: string[] = [];

  idorden: number;
  cliente_idcliente: number;
  fecha: string;
  hora: string;
  fechaEntregaEstimada: string;
  horaEntregaEstimada: string;
  fechaEntregaReal: string;
  horaEntregaReal: string;
  manoObra: number;
  subtotal: number;
  total: number;
  adeudo: number;
  factura: boolean;
  vehiculotaller_idvehiculotaller: number;
  comentarios: string;
clienteRewriteable: boolean = false;
vehiculotallerRewriteable: boolean = false;

  modalHeader: string;
  data: any;
  form: FormGroup;
  submitted: boolean = false;

  cliente_idclienteAC: AbstractControl;
  fechaAC: AbstractControl;
  horaAC: AbstractControl;
  fechaEntregaEstimadaAC: AbstractControl;
  horaEntregaEstimadaAC: AbstractControl;
  fechaEntregaRealAC: AbstractControl;
  horaEntregaRealAC: AbstractControl;
  manoObraAC: AbstractControl;
  subtotalAC: AbstractControl;
  totalAC: AbstractControl;
  adeudoAC: AbstractControl;
  facturaAC: AbstractControl;
  vehiculotaller_idvehiculotallerAC: AbstractControl;
  comentariosAC: AbstractControl;
  constructor(
      private service: OrdensService,
      private authService: AuthService, 
      private clientesService: ClientesService,
      private vehiculotallersService: VehiculotallersService,
      fb: FormBuilder,
      private toastrService: ToastrService,
      private authLocalstorage: AuthLocalstorage,
      dialogService: DialogService,
  ) {
  super(dialogService);
  this.form = fb.group({
    'cliente_idclienteAC' : ['',Validators.compose([Validators.required,Validators.maxLength(11)])],
    'fechaAC' : [''],
    'horaAC' : [''],
    'fechaEntregaEstimadaAC' : [''],
    'horaEntregaEstimadaAC' : [''],
    'fechaEntregaRealAC' : [''],
    'horaEntregaRealAC' : [''],
    'manoObraAC' : ['',Validators.compose([Validators.maxLength(11)])],
    'subtotalAC' : [''],
    'totalAC' : [''],
    'adeudoAC' : [''],
    'facturaAC' : [''],
    'vehiculotaller_idvehiculotallerAC' : ['',Validators.compose([Validators.required,Validators.maxLength(11)])],
    'comentariosAC' : ['',Validators.compose([Validators.maxLength(355)])],
  });
  this.cliente_idclienteAC = this.form.controls['cliente_idclienteAC'];
  this.fechaAC = this.form.controls['fechaAC'];
  this.horaAC = this.form.controls['horaAC'];
  this.fechaEntregaEstimadaAC = this.form.controls['fechaEntregaEstimadaAC'];
  this.horaEntregaEstimadaAC = this.form.controls['horaEntregaEstimadaAC'];
  this.fechaEntregaRealAC = this.form.controls['fechaEntregaRealAC'];
  this.horaEntregaRealAC = this.form.controls['horaEntregaRealAC'];
  this.manoObraAC = this.form.controls['manoObraAC'];
  this.subtotalAC = this.form.controls['subtotalAC'];
  this.totalAC = this.form.controls['totalAC'];
  this.adeudoAC = this.form.controls['adeudoAC'];
  this.facturaAC = this.form.controls['facturaAC'];
  this.vehiculotaller_idvehiculotallerAC = this.form.controls['vehiculotaller_idvehiculotallerAC'];
  this.comentariosAC = this.form.controls['comentariosAC'];
    // Buscar permisos del usuario en el mdulo
    const user = this.authService.useJwtHelper();

    if (user.super) {
        this.clienteRewriteable = true;
        this.vehiculotallerRewriteable = true;
    } else {
        const userModules = this.authService.getUserModules();
        if (userModules[0]) {
            for (const element in userModules) {
                if (userModules.hasOwnProperty(element)) {
                    if (userModules[element].path === '/pages/clientes') {
                        this.clienteRewriteable = userModules[element].writeable;
                    }
                    if (userModules[element].path === '/pages/vehiculotallers') {
                        this.vehiculotallerRewriteable = userModules[element].writeable;
                    }
                } 
            }
        }
    }
  }
  ngOnInit() {
      this.getCliente();
      this.getVehiculotaller();
  }

  clienteAddModalShow() {
      const disposable = this.dialogService.addDialog(ClientesAddModalComponent)
      .subscribe( data => {
          if (data) {
              this.clienteShowToast(data);
          }
      }); 
  }

  clienteShowToast(result) {
      if (result.success) {
          this.toastrService.success(result.message);
          this.getCliente();
      } else {
          this.toastrService.error(result.message);
      }
  }
  vehiculotallerAddModalShow() {
      const disposable = this.dialogService.addDialog(VehiculotallersAddModalComponent)
      .subscribe( data => {
          if (data) {
              this.vehiculotallerShowToast(data);
          }
      }); 
  }

  vehiculotallerShowToast(result) {
      if (result.success) {
          this.toastrService.success(result.message);
          this.getVehiculotaller();
      } else {
          this.toastrService.error(result.message);
      }
  }
  getCliente() {
      this.clientesService.all()
      .subscribe(
          (data: any) => this._cliente = data.result,
      );
  }
  getVehiculotaller() {
      this.vehiculotallersService.all()
      .subscribe(
          (data: any) => this._vehiculotaller = data.result,
      );
  }
  confirm() {
      this.result = this.data;
      this.close();
  }
  onSubmit(values: OrdensInterface): void {
      this.submitted = true;
      if (this.form.valid) {
          this.service
              .update({
                  idorden: this.idorden,
                  cliente_idcliente: this.cliente_idcliente,
                  fecha: this.fecha,
                  hora: this.hora,
                  fechaEntregaEstimada: this.fechaEntregaEstimada,
                  horaEntregaEstimada: this.horaEntregaEstimada,
                  fechaEntregaReal: this.fechaEntregaReal,
                  horaEntregaReal: this.horaEntregaReal,
                  manoObra: this.manoObra,
                  subtotal: this.subtotal,
                  total: this.total,
                  adeudo: this.adeudo,
                  factura: this.factura,
                  vehiculotaller_idvehiculotaller: this.vehiculotaller_idvehiculotaller,
                  comentarios: this.comentarios,
              })
              .subscribe(
                  (data: any) => {
                      this.data = data;
                      this.confirm();
              });
          }
  }
}



