import * as _ from 'lodash';
import { Pipe, PipeTransform } from '@angular/core';
@Pipe({
    name: 'estadoactividad_estadoactividad_idestadoactividadDataFilter'
})
export class Estadoactividad_estadoactividad_idestadoactividadFilterPipe implements PipeTransform {
    transform(array: any[], query: string): any {
        if (query) {
            return _.filter(array, row => row.estadoactividad_estadoactividad_idestadoactividad.indexOf(query) > -1);
        }
        return array;
    }
}



