import { RegistradorasAddModalComponent } from './../../../../registradoras/components/registradoras-table/registradoras-add-modal/registradoras-add-modal.component';
import { RegistradorasInterface } from './../../../../registradoras/components/registradoras-table/registradoras.interface';
import { PrinterService } from './../../../../../shared/printer.service';
import { AuthService } from './../../../../../shared/auth.service';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { AuthLocalstorage } from './../../../../../shared/auth-localstorage.service';
import { PagosService } from './../pagos.service';
import { PagosInterface } from './../pagos.interface';
import { Component, OnInit } from '@angular/core';
import { FormGroup, AbstractControl, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { EstadosService } from './../../../../estados/components/estados-table/estados.service';
import { EstadosAddModalComponent } from './../../../../estados/components/estados-table/estados-add-modal/estados-add-modal.component';
import { ChofersService } from './../../../../chofers/components/chofers-table/chofers.service';
import { ChofersAddModalComponent } from './../../../../chofers/components/chofers-table/chofers-add-modal/chofers-add-modal.component';
import { ChofersInterface } from './../../../../chofers/components/chofers-table/chofers.interface';
import { ChofersResponseInterface } from './../../../../chofers/components/chofers-table/chofers-response.interface';

import { LiquidacionsService } from './../../../../liquidacions/components/liquidacions-table/liquidacions.service';
import { LiquidacionsAddModalComponent } from './../../../../liquidacions/components/liquidacions-table/liquidacions-add-modal/liquidacions-add-modal.component';
import { PagoliquidacionsInterface } from './../../../../pagoliquidacions/components/pagoliquidacions-table/pagoliquidacions.interface';
import { PagoliquidacionsService } from './../../../../pagoliquidacions/components/pagoliquidacions-table/pagoliquidacions.service';

import { PagofianzasService } from './../../../../pagofianzas/components/pagofianzas-table/pagofianzas.service';
import { PagofianzasInterface } from './../../../../pagofianzas/components/pagofianzas-table/pagofianzas.interface';


@Component({
  selector: 'add-service-modal',
  styleUrls: [('./pagos-add-modal.component.scss')],
  templateUrl: './pagos-add-modal.component.html'
})
export class PagosAddModalComponent extends DialogComponent<PagosInterface, any> implements OnInit, PagosInterface {
  _estado: string[] = [];
  _chofer: string[] = [];
  _liquidacion: string[] = [];


estadoRewriteable: boolean = false;
choferRewriteable: boolean = false;

  cantidadRecibida: number;
  cambio: number;
  kilometraje: number;
  fecha: string;
  hora: string;
  nota: string;
  descripcion: string;
  folio: string;
  liquidacion: number;
  chofer_idchofer: number;
  pagoliquidacion: any[];

  modalHeader: string;
  data: any;
  form: FormGroup;
  submitted: boolean = false;
  cantidadRecibidaAC: AbstractControl;
  kilometrajeAC: AbstractControl;
  fechaAC: AbstractControl;
  horaAC: AbstractControl;
  notaAC: AbstractControl;
  folioAC: AbstractControl;
  liquidacionAC: AbstractControl;
  chofer_idchoferAC: AbstractControl;
  pagoliquidacionAC: AbstractControl;

  constructor(
    private service: PagosService,
    private authService: AuthService, 
    private estadosService: EstadosService,
    private printerService: PrinterService,
    private chofersService: ChofersService,
    private liquidacionsService: LiquidacionsService,
    private pagoliquidacionsService: PagoliquidacionsService,
    fb: FormBuilder,
    private toastrService: ToastrService,
    private authLocalstorage: AuthLocalstorage,
    dialogService: DialogService
  ) {
    super(dialogService);
    this.form = fb.group({
    'cantidadRecibidaAC' : [''],
    'kilometrajeAC' : ['', Validators.compose([ Validators.required, Validators.maxLength(11)])],
    'fechaAC' : [''],
    'horaAC' : [''],
    'notaAC' : ['', Validators.compose([ Validators.required, Validators.maxLength(60)])],
    'folioAC' : ['', Validators.compose([ Validators.required, Validators.maxLength(30)])],
    'liquidacionAC' : [''],
    'chofer_idchoferAC' : ['', Validators.compose([ Validators.required, Validators.maxLength(11)])],
    'pagoliquidacionAC' : [''],
    });
    this.cantidadRecibidaAC = this.form.controls['cantidadRecibidaAC'];
    this.kilometrajeAC = this.form.controls['kilometrajeAC'];
    this.fechaAC = this.form.controls['fechaAC'];
    this.horaAC = this.form.controls['horaAC'];
    this.notaAC = this.form.controls['notaAC'];
    this.folioAC = this.form.controls['folioAC'];
    this.liquidacionAC = this.form.controls['liquidacionAC'];
    this.chofer_idchoferAC = this.form.controls['chofer_idchoferAC'];
    this.pagoliquidacionAC = this.form.controls['pagoliquidacionAC'];
    
    // Buscar permisos del usuario en el mdulo
    const user = this.authService.useJwtHelper();

    if (user.super) {
        this.estadoRewriteable = true;
        this.choferRewriteable = true;
    } else {
        const userModules = this.authService.getUserModules();
        if (userModules[0]) {
            for (const element in userModules) {
                if (userModules.hasOwnProperty(element)) {
                    if (userModules[element].path === '/pages/estados') {
                        this.estadoRewriteable = userModules[element].writeable;
                    }
                    if (userModules[element].path === '/pages/chofers') {
                        this.choferRewriteable = userModules[element].writeable;
                    }
                } 
            }
        }
    }
  }
  ngOnInit() {
      this.getEstado();
      this.getChofer();
      this.getLiquidacion();

    // FECHA Y HORA ACTUAL
    const date = this.authLocalstorage.getCurrentDateAndHour();
    this.fecha = date.fecha;
    this.hora = date.hora;

    this.cantidadRecibida = 0;
    this.cambio = 0;
  }

  calcCambio() {
    this.cambio = this.cantidadRecibida - this.liquidacion;
  }
  estadoAddModalShow() {
      const disposable = this.dialogService.addDialog(EstadosAddModalComponent)
      .subscribe( data => {
          if (data) {
              this.estadoShowToast(data);
          }
      }); 
  }
  estadoShowToast(result) {
      if (result.success) {
          this.toastrService.success(result.message);
          this.getEstado();
      } else {
          this.toastrService.error(result.message);
      }
  }
  choferAddModalShow() {
      const disposable = this.dialogService.addDialog(ChofersAddModalComponent)
      .subscribe( data => {
          if (data) {
              this.choferShowToast(data);
          }
      }); 
  }
  choferShowToast(result) {
      if (result.success) {
          this.toastrService.success(result.message);
          this.getChofer();
      } else {
          this.toastrService.error(result.message);
      }
  }
  getEstado() {
      this.estadosService.all()
      .subscribe(
          (data: any) => this._estado = data.result,
      );
  }
  liquidacionAddModalShow() {
      const disposable = this.dialogService.addDialog(LiquidacionsAddModalComponent)
      .subscribe( data => {
          if (data) {
          this.liquidacionShowToast(data);
          }
      });
  }
  liquidacionShowToast(result) {
      if (result.success) {
          this.toastrService.success(result.message);
          this.getLiquidacion();
      } else {
          this.toastrService.error(result.message);
      }
  }
  getLiquidacion() {
      this.liquidacionsService.allAdeudandoFromIdChofer(this.chofer_idchofer)
      .subscribe(
          (data: any) => this._liquidacion = data.result,
      );
  }
  getChofer() {
      this.chofersService.all()
      .subscribe(
          (data: any) => this._chofer = data.result,
      );
  }
  confirm() {
    this.result = this.data;
    this.close();
  }

  registradoraIngreso(destino: string) {

    // DATOS DE USUARIO LOGEADO
    const user = this.authService.useJwtHelper();

    const registradora: RegistradorasInterface = {
        'idregistradora': null,
        'montoPagando': this.liquidacion,
        'montoIngresa': this.cantidadRecibida,
        'montoEgresa': this.cambio,
        'fecha': this.fecha,
        'hora': this.hora,
        'empleado_idempleado': user.idempleado,
        'destino': destino };

    const disposable = this.dialogService.addDialog(RegistradorasAddModalComponent, registradora)
    .subscribe( data => {
        if (data) {
            if (data.success) {
                this.toastrService.success(data.message);
            } else {
                this.toastrService.error(data.message);
            }
        }
    });
 }

 onSubmit(values: PagosInterface): void {
    this.submitted = true;
    if (this.form.valid) {

      // VERIFICA EN BACK KILOMETRAJE SI ESTA EN RANGO DE TABLA MANTENIMIENTO y 
      // ACTUALIZA ESTADO DE PERMISOTAXIASIGNADO A ALERTA-MANTENIMIENTO
      const pago = { 
                  cantidadRecibida: this.cantidadRecibida,
                  cambio: this.cambio,
                  kilometraje: this.kilometraje,
                  fecha: this.fecha,
                  hora: this.hora,
                  nota: this.nota,
                  cantPagada: this.liquidacion,
                  folio: this.folio,
                  liquidacion: this.liquidacion,
                  chofer_idchofer: this.chofer_idchofer,
                  estado_idestado: 8 };
      this.service
        .insert(pago)
        .subscribe(
            (data: any) => {
              if (data.success) {
                this.data = data;
                this.confirm();

                /*// Paga todas las liquidaciones seleccionadas, liquidaciones, total a pagar, idpago y idchofer se envan
                this.pagoliquidacionsService.pagoLiquidaciones(
                    this.pagoliquidacion, 
                    this.liquidacion, 
                    data.result.insertId, 
                    this.chofer_idchofer)
                .subscribe(
                    (dataLiquidaciones: any) => {

                        const _liquidacionesPagadas = dataLiquidaciones.result;

                        // OBTIENE MONTO ADEUDANDO LIQUIDACIONES DE CHOFER
                        this.liquidacionsService.adeudoFromIdChofer(this.chofer_idchofer)
                            .subscribe(
                                (_data: any) => {
                                    const deudaliquidacion = _data.result[0].deudaliquidacion;

                                    // ACTUALIZA MONTO ADEUDANDO LIQUIDACIN A CHOFER
                                    const chofer: ChofersInterface = {
                                        'idchofer': this.chofer_idchofer,
                                        'deudaliquidacion': deudaliquidacion };

                                    this.chofersService.update(chofer)
                                        .subscribe(
                                            (_result: any) => {

                                                pago['_deudaliquidacion'] = deudaliquidacion;

                                                // Imprime pago total a liquidaciones
                                                // IMPRIMIR / ABRIR CAJA Pago Liquidacin
                                                
                                                const html = this.printerService.printPago(pago, this.pagoliquidacion, _liquidacionesPagadas);
                                                
                                                const vent = window.open(' ', 'Pago de liquidacin', 'location=0,status=0,scrollbars=0');
                                                vent.document.write(html);
                                                vent.document.close();
                                                vent.print();
                                                vent.close();
                                                
                                                this.registradoraIngreso('LIQUIDACIONES');

                                                this.data = _result;
                                                this.confirm();
                                            });
                                });
                    });
*/
              } else {
                  this.data = data;
                  this.confirm();

                    if (data) {

                            this.toastrService.error(data.message);
                 
                    }
              }
            });
/**/
    }

  }
}



